<template>
  <div class="shoppingbag">
    <van-nav-bar
      v-if="bagProducts.length>0"
      title="购物袋"
      left-text="返回"
      @click-left="back()"
      :right-text="isEdit ? '编辑' : '完成'"
      left-arrow
      fixed
      @click-right="editProducts()"
    />
    <van-nav-bar
      v-if="bagProducts.length<=0"
      title="购物袋"
      left-text="返回"
      @click-left="back()"
      left-arrow
      fixed
    />
    <div class="product-item">
      <div class="product-box" v-for="(item,index) in bagProducts" :key="index">
        <van-swipe-cell class="clearfix" :disabled="isEdit">
          <van-cell :border="false">
            <van-checkbox
              checked-color="#3585f9"
              class="fl"
              v-model="item.isChecked"
              @click="selectOne"
            ></van-checkbox>
            <div class="menu-box fl">
              <div class="menu-list clearfix">
                <div class="menu-img fl">
                  <img class="auto-img" :src="item.small_img" alt />
                </div>
                <div class="menu-content fl clearfix">
                  <div class="menu-name">
                    {{item.name}}
                    <span class="spec">{{item.rule}}</span>
                  </div>
                  <div class="menu-enname">{{item.enname}}</div>
                  <div class="menu-pri fl">￥{{item.price}}</div>
                  <van-stepper
                    theme="round"
                    button-size="20"
                    disable-input
                    class="fr"
                    v-model="item.count"
                    @change="modifyCount(item)"
                  />
                </div>
              </div>
            </div>
          </van-cell>
          <template #right>
            <van-button square type="danger" text="删除" @click="deleteOneProduct(item.sid,index)" />
          </template>
        </van-swipe-cell>
        <van-divider />
      </div>
    </div>
    <div v-if="bagProducts.length>0">
      <van-submit-bar
        :price="total"
        button-text="提交订单"
        button-color="#3585f9"
        v-show="isEdit"
        @submit="getOrder"
      >
        <van-checkbox v-model="allChecked" @click="selectAll">全选</van-checkbox>
      </van-submit-bar>
      <van-submit-bar
        button-text="删除所选订单"
        button-color="#3585f9"
        v-show="!isEdit"
        @submit="deleteCheckProduct()"
      >
        <van-checkbox v-model="allChecked" @click="selectAll">全选</van-checkbox>
      </van-submit-bar>
    </div>
    <div v-if="bagProducts.length<=0">
      <van-empty description="您还没有加入商品到购物袋">
        <van-button round class="bottom-button" @click="goShopping">前往选购</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import "../mainViews/less/shoppingbag.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      allChecked: false,
      isEdit: true,
      bagProducts: [],
      total: 0,
    };
  },
  created() {
    this.findAllproducts();
  },
  methods: {
    editProducts() {
      this.isEdit = !this.isEdit;
    },
    back() {
      this.$router.go(-1);
    },
    goShopping() {
      this.$router.push({ name: "Menu" });
    },
    goSubmitOrder() {
      this.$router.push({ name: "SubmitOrder" });
    },
    //查询购物袋里面的商品
    findAllproducts() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
      
          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              v.isChecked = false;
            });

            this.bagProducts = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //全选
    selectAll() {
      this.bagProducts.map((v) => {
        v.isChecked = this.allChecked;
      });
      this.getTotal();
    },
    //单选
    selectOne() {
      this.getTotal();
      for (let i = 0; i < this.bagProducts.length; i++) {
        if (!this.bagProducts[i].isChecked) {
          this.allChecked = false;
          return;
        }
        this.allChecked = true;
      }
    },
    //删除购物袋商品
    deleteShopBagPro(sids) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        tokenString: tokenString,
        sids: JSON.stringify(sids),
      };
      data = utils.queryString(data);
      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data,
      });
    },
    //单个删除
    deleteOneProduct(sid, index) {
     
      this.deleteShopBagPro([sid])
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 7000) {
          
            this.bagProducts.splice(index, 1);
            this.$toast(result.data.msg);
          } else {
            this.$toast(result.data.msg);
          }
          this.getTotal();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //删除勾选的
    deleteCheckProduct() {
      let sids = [];
      this.bagProducts.map((v) => {
        //查找勾选的
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });
      this.deleteShopBagPro(sids)
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 7000) {
            for (let i = this.bagProducts.length - 1; i >= 0; i--) {
              if (this.bagProducts[i].isChecked) {
                this.bagProducts.splice(i, 1);
              }
            }
          }
          this.$toast(result.data.msg);
        })
        .catch((err) => {});
      this.getTotal();
    },
    //修改数量
    modifyCount(item) {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        tokenString: tokenString,
        sid: item.sid,
        count: item.count,
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data,
      }).then((result) => {
        this.$toast.clear();

        if (result.data.code == 6000) {
        }
        this.getTotal();
      });
    },
    //计算总价
    getTotal() {
      this.total = 0;
      this.bagProducts.map((v) => {
        if (v.isChecked) {
          this.total += v.price * v.count;
        }
      });

      this.total = this.total * 100;
    },
    //提交订单
    getOrder() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      let sids = []; //根据sid来查找需要购买的商品
      this.bagProducts.map((v) => {
        if (v.isChecked) {
          sids.push(v.sid);
        }
      });
      if (sids.length == 0) {
        this.$toast("您还没有勾选任何商品");
        return;
      }
      sids = sids.join("-");
      this.$router.push({ name: "SubmitOrder", query: { sids: sids } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>